@import url(https://fonts.googleapis.com/css2?family=Andada+Pro&family=Comfortaa:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Tangerine);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background-color: rgb(139, 79, 112);
}
.navbar-brand {
  font-family: "Comfortaa", cursive;
  display: flex;
}

.navbar-brand > h2 {
  margin-left: 4px;
}

.homepage__container {
  display: flex;
  justify-content: space-around;
}

.db__container {
  margin-top: 10px;
  text-align: center !important;
  height: 20vh;
}

.dbcontenet__head {
  color: maroon;
  font-family: 'Tangerine', serif;
  font-size: 50px;
}

.heading__container > p {
  color: blue;
  font-family: 'Playfair Display', serif;
  font-size: 30px;
}


.db__container > a {
  background-color: crimson;
  border: solid 1px green;
}

.footer__container > p {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
}
#dbcard__container {
  margin: 1rem 2rem;
}

#dbcard__body > h5 {
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  color: black;
  font-size: 15px;
}

#db__notice {
  background-color: red;
  color: white;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  text-align: center;
  border-radius: 10px;
}

#addsongs__container {
  display: flex;
  justify-content: space-between;
  margin: 0 2rem;
}

#addsongs__container > a > button {
  background-color: black;
  border: black;
  font-weight: bold;
  padding: 9px 5px;
  color: green;
}

#addsongs__container > a > button:hover {
  border: black;
  border-radius: 1rem 1rem;
  background-color: rgb(46, 46, 46);
  color: white;
}

#dbcard__body > p {
  text-align: center;
}

.db-loader {
  text-align: -webkit-center;
}

#dbsearch__container {
  text-align: -webkit-center;
}

#dbsearch__container > hr {
  color: grey;
  margin-top: 30px;
  border: solid 1px;
}

#addcard__container {
  margin: 2rem;
}

#addcard__container > .row > h1 {
  text-align: -webkit-center;
}

#addcard__body > h5 {
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  color: black;
  font-size: 15px;
}

#searchcard__container {
  display: flex;
  justify-content: space-between;
}

#searchcard__container > a {
  background-color: black;
  border: black;
  font-weight: bold;
  color: green;
  text-decoration: none;
}

#searchcard__container > a:hover {
  border: black;
  transition: 900ms;
  color: white;
}

